.container {
    height: 10vh;
    display: flex;
    align-items: center;
    justify-content: center;
}

.slider {
    position: relative;
    width: 200px;
}

.slider__track,
.slider__range,
.slider__left_value,
.slider__right_value {
    position: absolute;
}

.slider__track,
.slider__range {
    border-radius: 3px;
    height: 5px;
}

.slider__track {
    background-color: #21268F;
    opacity: 0.5;
    width: 100%;
    z-index: 1;
}

.slider__range {
    background-color: #21268F;
    z-index: 2;
}

.slider__left_value,
.slider__right_value {
    color: #dee2e6;
    font-size: 12px;
    margin-top: 20px;
}

.slider__left_value {
    left: 6px;
}

.slider__right_value {
    right: -4px;
}

/*Remove the default appearance */
.thumb,
.thumb::-webkit-slider-thumb {
    -webkit-appearance: none;
    -webkit-tap-highlight-color: transparent;
}

.thumb {
    pointer-events: none;
    position: absolute;
    height: 0;
    width: 200px;
    outline: none;

}

.thumbZIndex3 {
    z-index: 3;
}

.thumbZIndex4 {
    z-index: 4;
}

.thumbZIndex5 {
    z-index: 5;
}

.thumb::-webkit-slider-thumb {
    background-color: #f1f1f7;
    border: 4px solid #21268F;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px #21268F;
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
}

.thumb::-moz-range-thumb {
    background-color: #f1f5f7;
    border: none;
    border-radius: 50%;
    box-shadow: 0 0 1px 1px #ced4da;
    cursor: pointer;
    height: 18px;
    width: 18px;
    margin-top: 4px;
    pointer-events: all;
    position: relative;
}